.navegacion{
    background-color: var(--azulOscuro);
    margin-bottom: 2rem;
    max-width:100%;

    .navegacion-principal{
        display: flex;
        justify-content: left ;
        gap: 2rem;
        &:hover {
            background-color: var($fucsia);
        }
    }

    .nav-enlace-inicio{
        text-transform: uppercase;
    }

    .nav-enlace{
        text-decoration: none;
        text-align: center;
        color: var(--blanco);
        font-size: 2rem;
        font-weight: bold;
        padding: 1rem;
        text-transform: capitalize;
    }

}


// Por revisar
.nav-enlace__form{
    margin-bottom: 0;
}
.nav-enlace__form p{
    margin-bottom: 0;
    margin-top: 0;
}
