html {
  box-sizing: border-box;
  font-size: 62.5%; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-size: 16px;
  font-family: "Sarabun", sans-serif; }

a {
  text-decoration: none; }

.centrado {
  max-width: 120rem;
  margin: 0 auto;
  background-image: linear-gradient(to top, var(--grisClaro) 0%, var(--blanco) 100%);
  background-repeat: no-repeat;
  /*No repetir la imagen de fondo*/ }

.sombra {
  -webkit-box-shadow: 0px 0px 2px 0px rgba(112, 112, 112, 0.61);
  -moz-box-shadow: 0px 0px 2px 0px rgba(112, 112, 112, 0.61);
  box-shadow: 0px 0px 2px 0px rgba(112, 112, 112, 0.61);
  background-color: var(--blanco);
  padding: 2rem;
  border-radius: 1rem; }

.navegacion {
  background-color: var(--azulOscuro);
  margin-bottom: 2rem;
  max-width: 100%; }
  .navegacion .navegacion-principal {
    display: flex;
    justify-content: left;
    gap: 2rem; }
    .navegacion .navegacion-principal:hover {
      background-color: var(#C70039); }
  .navegacion .nav-enlace-inicio {
    text-transform: uppercase; }
  .navegacion .nav-enlace {
    text-decoration: none;
    text-align: center;
    color: var(--blanco);
    font-size: 2rem;
    font-weight: bold;
    padding: 1rem;
    text-transform: capitalize; }

.nav-enlace__form {
  margin-bottom: 0; }

.nav-enlace__form p {
  margin-bottom: 0;
  margin-top: 0; }

.fondo {
  background-color: #DFE9F3;
  padding: 9rem;
  line-height: 1.2; }
  .fondo .contenedor-login {
    margin: auto;
    max-width: 85rem;
    position: relative; }
    .fondo .contenedor-login .grid-login {
      display: grid;
      grid-template-columns: 1fr 1fr;
      height: 50rem; }
  .fondo .color-back {
    background-color: #0998E9;
    border-radius: 2rem 0 0 2rem; }
    .fondo .color-back .mensaje-bienvenida {
      padding: 12rem 5rem; }
      .fondo .color-back .mensaje-bienvenida h1 {
        color: #DFE9F3;
        margin-top: 0;
        font-size: 6rem; }
        .fondo .color-back .mensaje-bienvenida h1::after {
          content: '';
          display: block;
          width: 13rem;
          height: .5rem;
          background-color: #C70039;
          margin-top: 1rem; }
      .fondo .color-back .mensaje-bienvenida p {
        color: #ffffff;
        font-size: 3rem;
        font-weight: 500; }
  .fondo .login {
    background-color: #ffffff;
    border-radius: 0 2rem 2rem 0;
    padding-top: 8rem; }
    .fondo .login h2 {
      text-align: center;
      font-size: 3.8rem;
      margin: 0 auto 0 auto;
      width: 10rem; }
      .fondo .login h2::after {
        content: '';
        display: block;
        width: 4rem;
        height: .5rem;
        background-color: #2b465f;
        margin: .6rem auto 0 auto; }

.circulo {
  width: 10rem;
  height: 10rem;
  position: absolute;
  border-radius: 100%; }
  .circulo.circulo-blanco {
    background-color: #ffffff;
    right: 45%;
    bottom: 5rem; }
  .circulo.circulo-azul {
    background-color: #0998E9;
    left: 45%;
    top: 5rem; }

.login__datos {
  display: flex;
  flex-direction: column;
  margin-top: 4rem;
  align-items: center;
  row-gap: 2rem; }
  .login__datos input {
    color: #2b465f;
    font-weight: 700;
    border-top: none;
    border-right: none;
    border-left: none;
    border-bottom: 1px solid #bad0e5;
    width: 25rem;
    height: 5rem; }

.boton.boton-fucsia {
  margin: 3rem;
  background-color: #C70039;
  border: none;
  color: #ffffff;
  border-radius: .5rem; }
  .boton.boton-fucsia:hover {
    cursor: pointer;
    background-color: #9e002d; }

.fondo-azul {
  background-color: #2b465f; }

.contenedor {
  background-color: #DFE9F3;
  max-width: 110rem;
  margin: 6rem auto;
  border: none;
  border-radius: 2rem;
  padding: 2rem 0; }
  .contenedor .div-menu {
    padding: 3rem 5rem 7rem;
    text-align: center; }
    .contenedor .div-menu h1 {
      color: #2b465f;
      margin: 0;
      font-size: 7rem; }
      .contenedor .div-menu h1 span {
        font-size: 10rem;
        color: #C70039; }
  .contenedor .contenedor-tareas {
    list-style: none;
    margin: 5rem auto 0 auto;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 70rem;
    row-gap: 5rem; }
    .contenedor .contenedor-tareas .tarea {
      justify-content: center; }
      .contenedor .contenedor-tareas .tarea p {
        margin: 0;
        font-weight: 700;
        color: #C70039; }
    .contenedor .contenedor-tareas .enlace__tarea img {
      max-width: 8rem; }

.contenedor .contenedor__datos-informacion {
  background-color: #ffffff;
  border: none;
  border-radius: 1rem;
  max-width: 90%;
  margin: 0 auto;
  padding: 1rem; }
  .contenedor .contenedor__datos-informacion .datos-cliente {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 1.5rem;
    width: 70rem; }
    .contenedor .contenedor__datos-informacion .datos-cliente .dato-cliente {
      color: #C70039;
      text-align: left;
      font-weight: 900;
      width: 10rem;
      display: flex; }
      .contenedor .contenedor__datos-informacion .datos-cliente .dato-cliente input {
        border: none;
        outline: none; }

.contenedor-table {
  max-width: 90%;
  margin: 1rem auto;
  background-color: #ffffff;
  padding: 1rem; }
  .contenedor-table .table-top {
    display: grid;
    grid-template-columns: repeat(2, 1fr); }
  .contenedor-table .search {
    max-width: 28rem;
    background-color: #DFE9F3;
    border-radius: 3rem;
    margin-left: auto;
    margin-right: auto;
    height: 4rem;
    display: flex;
    justify-content: center; }
    .contenedor-table .search .search-flex {
      display: flex;
      align-items: center; }
      .contenedor-table .search .search-flex input {
        background-color: #DFE9F3;
        border: none;
        border-radius: 3rem;
        outline: none;
        height: 3rem;
        text-align: center;
        width: 18rem; }
      .contenedor-table .search .search-flex img {
        width: 2rem; }
        .contenedor-table .search .search-flex img:hover {
          cursor: pointer; }
  .contenedor-table .view-selected {
    border-radius: 5rem;
    background-color: #2b465f;
    width: 13rem;
    color: #ffffff; }

.table {
  text-align: center;
  width: 95%;
  margin: 1rem auto; }
  .table .table-header th {
    font-size: 3rem; }
  .table tr .th-total {
    width: 25%; }
  .table tr .th-quant {
    width: 20%; }
  .table tr td {
    font-size: 2rem; }
    .table tr td .quant {
      width: 8rem; }

.total {
  color: #94002a;
  font-weight: 900;
  width: 18rem;
  border-top: 0;
  border-right: 0;
  border-left: 0;
  border-bottom: 1px solid #bad0e5;
  outline: none; }

.boton-add {
  border: none;
  background-color: #0998E9;
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 700;
  padding: .5rem 1rem;
  border-radius: .5rem; }
  .boton-add:hover {
    cursor: pointer; }

.boton-azul {
  background-color: #0998E9; }

.boton-fucsia {
  background-color: #C70039; }

.inferior {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .inferior .boton-table {
    border-radius: 1rem;
    border: none;
    color: #ffffff;
    padding: 1rem 2rem;
    font-size: 2rem;
    font-weight: 700; }
  .inferior .total {
    border-bottom: 1px solid #3a6a99;
    font-size: 4rem; }
  .inferior a {
    font-size: 2.5rem;
    color: #0778b8;
    font-weight: 700;
    border-bottom: 1px solid #0778b8; }
