.fondo-azul{
    background-color: $azulOscuro;
}

.contenedor{
    background-color: $gris;
    max-width: 110rem;
    margin: 6rem auto;
    border: none;
    border-radius: 2rem;
    padding: 2rem 0;
    // overflow: hidden;
    
    .div-menu {
        padding: 3rem 5rem 7rem;
        text-align: center;
        
        h1 {
            color: $azulOscuro;
            margin: 0;
            font-size: 7rem;
            span {
                font-size: 10rem;
                color: $fucsia;
            }
        }
    }

    .contenedor-tareas {
        list-style: none;
        margin: 5rem auto 0 auto;
        padding: 0;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        width: 70rem;
        row-gap: 5rem;
        

        .tarea {
            justify-content: center;
            

            p {
                margin: 0;
                font-weight: $bold;
                color: $fucsia;
            }
        }

        .enlace__tarea {
            img {
                max-width: 8rem ;
            }
        }
    }

}