.fondo{
    background-color: $gris;
    padding: 9rem ;
    line-height: 1.2;
    .contenedor-login {
        margin: auto;
        max-width: 85rem;
        position: relative;

        .grid-login {
            display: grid;
            grid-template-columns: 1fr 1fr;
            height: 50rem;
        }
    }

    .color-back {
        background-color: $azul;
        border-radius: 2rem 0 0 2rem;

        .mensaje-bienvenida {
            padding: 12rem 5rem;

            h1 {
                color: $gris;
                margin-top: 0;
                font-size: 6rem;
                &::after {
                    content: '';
                    display: block;
                    width: 13rem;
                    height: .5rem;
                    background-color: $fucsia;
                    margin-top: 1rem;
                } 
            }

            p  {
                color: $blanco;
                font-size: 3rem;
                font-weight: $regular;
            }
        }
    }

    .login {
        background-color:$blanco;
        border-radius: 0 2rem 2rem 0;
        padding-top: 8rem;
        h2 {
            text-align: center;
            font-size: 3.8rem;
            margin: 0 auto 0 auto ;
            width: 10rem;
            &::after {
                content: '';
                display: block;
                width: 4rem;
                height: .5rem;
                background-color: $azulOscuro;
                margin: .6rem auto 0 auto;
                
            }
        }
    }
}

.circulo {
    width: 10rem;
    height: 10rem;
    position: absolute;
    border-radius: 100%;
    &.circulo-blanco{
        background-color: $blanco;
        right: 45%;
        bottom: 5rem;
    }
    &.circulo-azul{
        background-color: $azul;
        left: 45%;
        top: 5rem;
    }
}


.login__datos{
    display: flex;
    flex-direction: column;
    margin-top: 4rem;
    align-items: center;
    row-gap: 2rem;
    input {
        color: $azulOscuro;
        font-weight: $bold;
        border-top:none;
        border-right:none;
        border-left:none;
        border-bottom: 1px solid darken($gris,10);
        width: 25rem;
        height: 5rem;
    }
}

// Utilidades
.boton {
    &.boton-fucsia {
        margin: 3rem ;
        background-color: $fucsia;
        border: none;
        color: $blanco;
        border-radius: .5rem;
        &:hover{
            cursor: pointer;
            background-color: darken($fucsia, 8);
        }
    }
}


