.contenedor{

    .contenedor__datos-informacion {
        background-color: $blanco;
        border: none;
        border-radius: 1rem;
        max-width: 90%;
        margin: 0 auto;
        padding: 1rem;

        .datos-cliente {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            row-gap: 1.5rem;
            width:70rem;
        
            .dato-cliente {
                color: $fucsia;
                text-align: left;
                font-weight: $black;
                width: 10rem;
                display: flex;

                input {
                    border: none;
                    outline: none;
                }
            }
        }
    }
}

// GIT

.contenedor-table {
    max-width: 90%;
    margin: 1rem auto;
    background-color: $blanco;
    padding: 1rem;

    .table-top {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
        .search {
            max-width: 28rem;
            background-color: $gris;
            border-radius: 3rem;
            margin-left: auto;
            margin-right: auto;
            height: 4rem;
            display: flex;
            justify-content: center;
            .search-flex {
                display: flex;
                align-items: center; // rev
                input {
                    background-color: $gris;
                    border: none;
                    border-radius: 3rem;
                    outline: none;
                    height: 3rem;
                    text-align: center;
                    width: 18rem;
                }
                img {
                    width: 2rem;
                    &:hover {
                        cursor: pointer;
                    }
                }
            }
        }

        .view-selected {
            border-radius: 5rem;
            background-color: $azulOscuro;
            width: 13rem;
            color: $blanco;
        }
    

}

.table {
    text-align: center;
    width: 95%;
    margin: 1rem auto;

    .table-header {
        th {
            font-size: 3rem;
        }
    }

    tr {
        .th-total {
            // width: 24rem;
            width: 25%;
        }
        .th-quant {
            width: 20%;
        }

        td {
            font-size: 2rem;

            .quant {
                width: 8rem;
            }
        }  
    }
}
.total {
    color: darken($fucsia,10);
    font-weight: $black;
    width: 18rem;
    border-top: 0;
    border-right: 0;
    border-left: 0;
    border-bottom: 1px solid darken($gris,10);
    outline: none;
}

.boton-add {
    border: none;
    background-color: $azul;
    color: $blanco;
    font-size: 1.5rem;
    font-weight: $bold;
    padding: .5rem 1rem;
    border-radius: .5rem;
    &:hover {
        cursor: pointer;
    }
}
.boton-azul {
    background-color: $azul;
}
.boton-fucsia {
    background-color: $fucsia;
}

.inferior {
    display: flex;
    justify-content: space-between; 
    align-items: center;
    .boton-table {
        border-radius: 1rem;
        border: none;
        color: $blanco;
        padding: 1rem 2rem;
        font-size: 2rem;
        font-weight: $bold;
    }
    .total {
        border-bottom: 1px solid darken($gris,50);
        font-size: 4rem;
    }

    a {
        font-size: 2.5rem;
        color: darken($azul,10);
        font-weight: $bold;
        border-bottom: 1px solid darken($azul,10);
    }

}