html{
    box-sizing: border-box;
    font-size: 62.5%;
}
*,*:before, *:after {
    box-sizing: inherit;
}

body{
    font-size: 16px;
    font-family: $fuentePrincipal;
}

a {
    text-decoration: none;
}

.centrado {
    max-width: 120rem;
    margin: 0 auto;
    background-image: linear-gradient(to top, var(--grisClaro) 0%, var(--blanco) 100%); // Degradado de la imagen
    background-repeat: no-repeat; /*No repetir la imagen de fondo*/
}
.sombra{
    -webkit-box-shadow: 0px 0px 2px 0px rgba(112,112,112,0.61);
    -moz-box-shadow: 0px 0px 2px 0px rgba(112,112,112,0.61);
    box-shadow: 0px 0px 2px 0px rgba(112,112,112,0.61);
    background-color: var(--blanco);
    padding: 2rem;
    border-radius: 1rem;
}